@import "./GlobalAnimation.scss";
@import "../node_modules/rfs/scss";

.section-btn {
    width: 0%;
    height: 100%;
    background-color: #796CFF !important;
    transition: width $section-button-animation-duration $section-cubic-bezier, color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out !important;
    
    .f-btn-icon {
        width: 0%;
    }

    h3 {
        font-size: 0pt;
        transition: font-size $section-button-animation-duration $section-cubic-bezier !important;
        display: flex; 
        margin-left: -4px;
        display: flex;
        
    }

    h3.presenting {
        font-size: 15pt !important;
        transition: font-size $section-button-animation-duration $section-cubic-bezier !important;
    }

}

.section-btn.presenting {
    width: 100%;
}


%button-hint-shared {
    color: #353064;
    writing-mode: vertical-rl;

}

.button-hint-top {
    @extend %button-hint-shared;
    margin-left: -10%;
}

.button-hint-bottom {
    @extend %button-hint-shared;
    text-orientation: sideways;
    transform: scale(-1, -1);
    margin-right: 10px;
}