// theme color --- hero
$hero-bg-color: #41516D;
$peoject-card-list-bg-color: #283040;
$f-card-bg-color: #353064;
$ai-card-bg-color: #2d2828;
$coe-card-bg-color: #242342;
$footer-card-bg-color: #283040;

@import url('https://fonts.googleapis.com/css?family=Noto+Sans+TC:100,400,900&display=swap&subset=chinese-traditional');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,700,800&display=swap');
@import "../node_modules/rfs/scss";
@import "./GlobalAnimation.scss";
// @import "~rfs/scss"


// global
%section-global {
    height: 100vh;
    width: 100%;
}

#pagerContainer {
    overflow: hidden;
}

#pageWrapper {
    @extend %section-global;
    transition: transform $fullpager-section-duration cubic-bezier(0.77, 0, 0.175, 1) 0s;
}

%en {
    font-family: 'Open Sans', sans-serif;
}

// tempoary attribute

h2 {
    padding: 0px;
    margin: 0px;
    color: aliceblue;
}

.btn-primary {
    background-color: green;
}

// section-setting

#section-hero {
    @extend %section-global;
    background-color: $hero-bg-color;
    position: relative;
    z-index: 1;

    #introTextH2 {
        position: absolute;
        top: 16vw;
        padding: 0vh 10vw 0 10vw;
        display: flex;
        align-items: flex-end;
        z-index: 20;
    }

}

#section-f {
    @extend %section-global;
    background-color: $f-card-bg-color;

}

#section-ai {
    @extend %section-global;
    background-color: $ai-card-bg-color;
}

#section-coe {
    @extend %section-global;
    background-color: $coe-card-bg-color;
}

#section-footer {
    @extend %section-global;
    background-color: $footer-card-bg-color;
}

// project card

.section-card-area {

    float: right;
    position: relative;
    display: flex;
    align-items: flex-end;

    .project-number {
        writing-mode: vertical-rl;
        text-orientation: mixed;
        transform: scale(-1, -1);
        color: #353064;
    }

    .section-card {
        margin: 10vh 0 0 0;
        height: 50vh;
        background-color: #353064;
        float: right;
        position: relative;
        transition: transform $fullpager-section-duration $section-cubic-bezier;
       


        .section-card-link {
            text-decoration: none !important;
            color: #796CFF;

            .article {
                height: 45vh;



            }

            %f-card-img {
                float: left;
            }

            .img-area {
                height: 50vh;

                .center-imgs {
                    height: 50vh;
                    width: fit-content;

                    #megaphone-icon-wraper {
                        height: 10vh;
                        width: inherit;

                        #megaphone-icon {
                            @extend %f-card-img;
                            height: inherit;
                        }
                    }

                    #project-mockup-wraper {
                        height: 20vh;
                        width: inherit;

                        #project-mockup {
                            @extend %f-card-img;
                            height: inherit;
                        }
                    }

                }

                #submit-icon,
                #location_icon {
                    @extend %f-card-img;
                    height: 4vh;
                    // margin-bottom: 10vh;
                    width: auto;
                }
            }

            .section-btn-container {
                height: inherit;

                .section-btn {
                    display: block;
                    border-radius: 0px;
                }
            }
        }
    }

    .section-card:hover {

        transform: translateX(-2.5%) scale(1.05);

    }

}

// colligraphy 

#mask-container {

    overflow: hidden;
    width: 100%;
    height: 100vh;
    position: absolute;

    #upper-text-mask {

        height: 40vh;
        width: 100%;
        position: absolute;
        top: 0%;

        #upperText {
            width: 100%;
            position: absolute;
            height: 55vw;
            top: -38vw;
        }
    }

    #lower-text-mask {

        height: 60vh;
        width: 100%;
        position: absolute;
        bottom: 0%;

        #lowerText {
            width: 100%;
            position: absolute;
            bottom: -19vw;
            height: 55vw;
        }
    }
}



%ui-text-shared {
    vertical-align: top;
}

$screenWidth: 51.7+14.6+51.7+36.8+26.346;

.u1Frame {
    @extend %ui-text-shared;
    margin-right: 1.75 / $screenWidth * 100vw;
}

.iFrame {
    @extend %ui-text-shared;
    margin-right: 19.677 / $screenWidth * 100vw;

}

.u2Frame {
    @extend %ui-text-shared;
    margin-right: -4.919 / $screenWidth * 100vw;
    position: absolute;
}

.xFrame {
    @extend %ui-text-shared;
    margin-right: 9.838 / $screenWidth * 100vw;
    float: right;
    position: relative;
    z-index: 10;
}

#upperXFrame {
    position: relative;
}

#lowerXFrame {
    position: relative;
}