@import "../node_modules/rfs/scss";

// fullpager attrbute
$fullpager-section-duration: 1000ms;
$css-animation-duration: 900ms;
$section-button-animation-duration: 600ms;
$section-cubic-bezier: cubic-bezier(.19,1.03,.81,.99);
$distance: 600px;

// Css Animation

%cssanimation-shared {
    animation-duration: $css-animation-duration;
    animation-fill-mode: both;
  }

.cssanimation {
    @extend %cssanimation-shared;
}

.cssanimation span { 
    @extend %cssanimation-shared;
    display: inline-block 
}

.leFadeInBottom span { 
    animation-name: leFadeInBottom
 }
@keyframes leFadeInBottom {
    from {
        opacity: 0;
        transform: translateY(50%);
    }
    to { opacity: 1 }
}

// the moveFrom animation



.moveFromBottom {
    animation-name: moveFromBottom;
    position: relative;
    z-index: 1;
}

@keyframes moveFromBottom {
    from {
        transform: translateY($distance)
    }
}

.moveFromTop {
    animation-name: moveFromTop
}

@keyframes moveFromTop {
    from {
        transform: translateY(-$distance)
    }
}



// global
%section-global {
    height: 100vh;
    width: 100%;
}

#pagerContainer {
    overflow: hidden;
}

#pageWrapper {
    @extend %section-global;
    transition: transform $fullpager-section-duration cubic-bezier(0.77, 0, 0.175, 1) 0s;
}

// text

%en {
    font-family: 'Open Sans', sans-serif;
}

%cn {
    font-family: 'Noto Sans TC', sans-serif;
}

.en-xblod {
    @extend %en;
    font-weight: 800;
}

.en-blod {
    @extend %en;
    font-weight: 700;
}

.en {
    @extend %en;
    font-weight: 400;
}

.en-light {
    @extend %en;
    font-weight: 300;
}

.cn-blod {
    @extend %cn;
    font-weight: 900;
}

.cn {
    @extend %cn;
    font-weight: 400;
}

.cn-light {
    @extend %cn;
    font-weight: 100;
}

#section-f-bg {

    h6 {
        @include font-size(0.7rem);
    }
    
    h5 {
        @include font-size(1.7rem);
    }
    
    h1 {
        @include font-size(4rem !important);
        z-index: 20 !important;
    }
    
    h3 {
        @include font-size(1.5rem);
    }
    
    hr {
        border-width: 10px;
        background-color: #796CFF;
        width: 6%;
    }
    
    p {
        @include font-size(0.9rem);
    
        @media (max-width: 480px) {
            @include font-size(0.65rem);
        }
    }

}